import React, { useState, useEffect } from "react";
import "semantic-ui-css/semantic.min.css";
import "date-fns";
import "./App.css";
import {
  Container,
  Segment,
  Header,
  Statistic,
  Table,
  Button,
  List,
  Dimmer,
  Loader,
  Accordion,
  Icon,
  Input,
} from "semantic-ui-react";
import { GoogleSpreadsheet } from "google-spreadsheet";
import "react-datepicker/dist/react-datepicker.css";
import {
  XYPlot,
  XAxis,
  YAxis,
  VerticalGridLines,
  HorizontalGridLines,
  VerticalBarSeries,
  Hint,
  DiscreteColorLegend,
} from "react-vis";

function ExcelDateToJSDate(serial) {
  var utc_days = Math.floor(serial - 25568);
  var utc_value = utc_days * 86400;
  var date_info = new Date(utc_value * 1000);

  var fractional_day = serial - Math.floor(serial) + 0.0000001;

  var total_seconds = Math.floor(86400 * fractional_day);

  var seconds = total_seconds % 60;

  total_seconds -= seconds;

  var hours = Math.floor(total_seconds / (60 * 60));
  var minutes = Math.floor(total_seconds / 60) % 60;

  var date_data = new Date(
    date_info.getFullYear(),
    date_info.getMonth(),
    date_info.getDate(),
    hours,
    minutes,
    seconds
  );

  return date_data;
}

function FechaString(fecha) {
  var ye = new Intl.DateTimeFormat("en", { year: "numeric" }).format(fecha);
  var mo = new Intl.DateTimeFormat("en", { month: "short" }).format(fecha);
  var da = new Intl.DateTimeFormat("en", { day: "2-digit" }).format(fecha);
  var fechaStr = `${da}-${mo}-${ye}`;
  return fechaStr;
}

function FechaStringShort(fecha) {
  var ye = new Intl.DateTimeFormat("en", { year: "numeric" }).format(fecha);
  var mo = new Intl.DateTimeFormat("en", { month: "numeric" }).format(fecha);
  var da = new Intl.DateTimeFormat("en", { day: "2-digit" }).format(fecha);
  var fechaStr = `${ye}-${mo < 10 ? "0" + mo : mo}-${da}`;
  return fechaStr;
}

function Home({ user }) {
  const [data, setData] = useState();
  const [hoy, setHoy] = useState();
  const [ayer, setAyer] = useState();
  const [mes, setMes] = useState();
  const [chartData, setChartData] = useState();
  const [chartMes, setChartMes] = useState();
  const [chartRubros, setChartRubros] = useState();
  const [mesAnterior, setMesAnterior] = useState();
  const [aplicable, setAplicable] = useState();
  const [prueba, setPrueba] = useState(false);
  const [saving, setSaving] = useState(false);
  const [activeIndex, setIndex] = useState(-1);
  const [areas, setAreas] = useState();
  const [leyendaMes, setLeyendaMes] = useState();
  const [leyenda, setLeyenda] = useState();
  //Fechas
  //fechas estan erradas hay que corregir
  const dias = Array.from({ length: 31 }, (_, index) => index + 1);
  const meses = Array.from({ length: 12 }, (_, index) => index + 1);
  const rubros = ["Seguridad", "Calidad", "Ambiental", "Bioseguridad"];
  const lista = [];
  for (var i = 0; i < rubros.length; i++) {
    lista.push({ title: rubros[i] });
  }
  let today = new Date();
  let currentDay = new Date();
  let yesterday = new Date(today);
  yesterday.setDate(yesterday.getDate() - 1);
  let wd = today.getDay();
  wd === 0 ? (wd = 7) : (wd = wd);
  //Fechas
  const [dataSemana, setDS] = useState();
  const [semana, setSemana] = useState(
    FechaStringShort(today.setDate(today.getDate() - wd + 1))
  );
  function handleSemana(fecha) {
    var anno = fecha.substring(0, 4);
    var mes = fecha.substring(5, 7);
    var dia = fecha.substring(8, 10);
    var d = new Date(anno, mes - 1, dia);
    let weekday = d.getDay();
    weekday === 0 ? (weekday = 7) : (weekday = weekday);
    d.setDate(d.getDate() - weekday + 1);
    setSemana(FechaStringShort(d));
    var selector = document.getElementById("semanas");
    selector.value = FechaStringShort(d);
  }

  const sacarAreas = async () => {
    var data = [];
    let datas = [];
    const creds = require("./sasso-app.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1N_nSzixyWg6OI7I7hTOQ-rfCpeln-pYV4FMtm17wIR0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[1705686078];
    const rows = await sheet.getRows();
    const cols = await sheet.columnCount;
    const encabezado = await sheet.headerValues;

    let lastr = rows.length + 1;
    var j, i;
    //await sheet.loadCells("A1:V" + lastr.toString());
    await sheet.loadCells({
      // GridRange object
      startRowIndex: 0,
      endRowIndex: lastr,
      startColumnIndex: 0,
      endColumnIndex: cols,
    });
    for (j = 1; j < lastr; j++) {
      data = [];
      for (i = 0; i <= cols - 1; i++) {
        window["Celda" + j + i] = sheet.getCell(j, i).value;
        data[`${encabezado[i]}`] = window["Celda" + j + i];
      }
      datas.push(data);
    }
    setAreas(datas);
  };

  const sacardata = async () => {
    var data = [];
    let datas = [];
    const creds = require("./sasso-app.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1N_nSzixyWg6OI7I7hTOQ-rfCpeln-pYV4FMtm17wIR0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[0];
    const rows = await sheet.getRows();
    const cols = await sheet.columnCount;
    const encabezado = await sheet.headerValues;

    let lastr = rows.length + 1;
    var j, i;
    //await sheet.loadCells("A1:V" + lastr.toString());
    await sheet.loadCells({
      // GridRange object
      startRowIndex: 0,
      endRowIndex: lastr,
      startColumnIndex: 0,
      endColumnIndex: cols,
    });
    for (j = 1; j < lastr; j++) {
      data = [];
      for (i = 0; i <= cols - 1; i++) {
        window["Celda" + j + i] = sheet.getCell(j, i).value;
        data[`${encabezado[i]}`] = window["Celda" + j + i];
      }
      datas.push(data);
    }
    setData(datas);
    setAplicable(
      datas.filter((item) => {
        return item.Aplica === "Y";
      })
    );
    setHoy(
      datas.filter((item) => {
        return (
          FechaStringShort(ExcelDateToJSDate(item.fecha)) ===
          FechaStringShort(currentDay)
        );
      })
    );
    setAyer(
      datas.filter((item) => {
        return (
          FechaStringShort(ExcelDateToJSDate(item.fecha)) ===
          FechaStringShort(yesterday)
        );
      })
    );
    setMes(
      datas.filter((item) => {
        return ExcelDateToJSDate(item.fecha).getMonth() === today.getMonth();
      })
    );
    let arr = [];
    for (var i = 0; i < dias.length; i++) {
      arr.push({
        x: dias[i],
        y: Math.ceil(
          datas
            .filter((item) => {
              return (
                ExcelDateToJSDate(item.fecha).getDate() === dias[i] &&
                ExcelDateToJSDate(item.fecha).getMonth() === today.getMonth()
              );
            })
            .reduce(function (a, b) {
              return a + b.ids;
            }, 0)
        ),
      });
    }
    setChartData(arr);
    let arr2 = [];
    for (var i = 0; i < meses.length; i++) {
      arr2.push({
        x: meses[i],
        y: Math.ceil(
          datas
            .filter((item) => {
              return ExcelDateToJSDate(item.fecha).getMonth() + 1 === meses[i];
            })
            .reduce(function (a, b) {
              return a + b.ids;
            }, 0)
        ),
      });
    }
    setChartMes(arr2);
    let arr3 = [];
    for (var j = 0; j < rubros.length; j++) {
      let temp = [];
      for (var i = 0; i < meses.length; i++) {
        temp.push({
          x: meses[i],
          y: Math.ceil(
            datas
              .filter((item) => {
                return (
                  ExcelDateToJSDate(item.fecha).getMonth() + 1 === meses[i] &&
                  item.rubro === rubros[j]
                );
              })
              .reduce(function (a, b) {
                return a + b.ids;
              }, 0)
          ),
        });
      }
      arr3.push(temp);
    }
    setChartRubros(arr3);
    setMesAnterior(
      datas.filter((item) => {
        return (
          ExcelDateToJSDate(item.fecha).getMonth() === today.getMonth() - 1
        );
      })
    );
  };
  useEffect(() => {
    if (!areas) {
      sacarAreas();
    }
  }, [areas]);
  useEffect(() => {
    if (data) {
      setDS(
        data.filter((item) => {
          return (
            FechaStringShort(ExcelDateToJSDate(item.semana)) === semana &&
            item.Aplica === "Y"
          );
        })
      );
    }
  }, [semana]);
  useEffect(() => {
    if (!data) {
      sacardata();
    }
    if (data) {
      setDS(
        data.filter((item) => {
          return (
            FechaStringShort(ExcelDateToJSDate(item.semana)) === semana &&
            item.Aplica === "Y"
          );
        })
      );
    }
  }, [data]);

  return (
    <Container>
      <Dimmer active={!data || saving} page>
        <Loader>{saving ? "Saving" : "Loading"}</Loader>
      </Dimmer>
      <Segment>
        <Header>
          <Input
            id="semanas"
            label="Semana"
            size="mini"
            type="date"
            defaultValue={semana}
            onChange={(e) => {
              handleSemana(e.target.value);
            }}
          />
        </Header>
        <Header>Mi Cultura</Header>
        {areas && dataSemana && (
          <Accordion>
            <Accordion.Title
              index={0}
              onClick={(e, titleProps) => {
                const { index } = titleProps;
                setIndex(activeIndex === index ? -1 : index);
              }}
              active={activeIndex === 0}
            >
              <Icon name="dropdown" />
              PLANTA MANUFACTURA PANAMA -Q-
              {(
                (1 -
                  dataSemana.filter((item) => {
                    return item.rubro === "Calidad";
                  }).length *
                    (0.22 / areas.length)) *
                100
              ).toFixed(2)}
              -S-
              {(
                (1 -
                  dataSemana.filter((item) => {
                    return item.rubro === "Seguridad";
                  }).length *
                    (0.19 / areas.length)) *
                100
              ).toFixed(2)}
              -E-
              {(
                (1 -
                  dataSemana.filter((item) => {
                    return item.rubro === "Ambiente";
                  }).length *
                    (0.27 / areas.length)) *
                100
              ).toFixed(2)}
              -B-
              {(
                (1 -
                  dataSemana.filter((item) => {
                    return item.rubro === "Bioseguridad";
                  }).length *
                    (0.8 / areas.length)) *
                100
              ).toFixed(2)}
            </Accordion.Title>
            <Accordion.Content active={activeIndex === 0}>
              {areas.map((area) => (
                <li>
                  {area.name}-Q-
                  {(
                    (1 -
                      dataSemana.filter((item) => {
                        return (
                          item.area === area.name && item.rubro === "Calidad"
                        );
                      }).length *
                        0.22) *
                    100
                  ).toFixed(2)}
                  -S-
                  {(
                    (1 -
                      dataSemana.filter((item) => {
                        return (
                          item.area === area.name && item.rubro === "Seguridad"
                        );
                      }).length *
                        0.19) *
                    100
                  ).toFixed(2)}
                  -E-
                  {(
                    (1 -
                      dataSemana.filter((item) => {
                        return (
                          item.area === area.name && item.rubro === "Ambiente"
                        );
                      }).length *
                        0.27) *
                    100
                  ).toFixed(2)}
                  -B-
                  {(
                    (1 -
                      dataSemana.filter((item) => {
                        return (
                          item.area === area.name &&
                          item.rubro === "Bioseguridad"
                        );
                      }).length *
                        0.8) *
                    100
                  ).toFixed(2)}
                </li>
              ))}
            </Accordion.Content>
          </Accordion>
        )}

        {hoy && ayer && mes && mesAnterior && <Header>Observaciones</Header>}
        {hoy && ayer && mes && mesAnterior && (
          <List horizontal>
            <List.Item>
              <List.Content>
                <b>
                  <h1>
                    {Math.ceil(
                      hoy.reduce(function (a, b) {
                        return a + b.ids;
                      }, 0)
                    )}
                  </h1>{" "}
                  Hoy
                </b>
              </List.Content>
            </List.Item>
            <List.Item>
              <List.Content>
                <b>
                  <h1>
                    {Math.ceil(
                      ayer.reduce(function (a, b) {
                        return a + b.ids;
                      }, 0)
                    )}
                  </h1>{" "}
                  Ayer
                </b>
              </List.Content>
            </List.Item>
            <List.Item>
              <List.Content>
                <b>
                  <h1>
                    {Math.ceil(
                      mes.reduce(function (a, b) {
                        return a + b.ids;
                      }, 0)
                    )}
                  </h1>{" "}
                  Mes Actual
                </b>
              </List.Content>
            </List.Item>
            <List.Item>
              <List.Content>
                <b>
                  <h1>
                    {Math.ceil(
                      mesAnterior.reduce(function (a, b) {
                        return a + b.ids;
                      }, 0)
                    )}
                  </h1>{" "}
                  Mes Anterior
                </b>
              </List.Content>
            </List.Item>
          </List>
        )}
      </Segment>
      {chartMes && (
        <Segment>
          <Header>Observaciones por Mes</Header>
          <XYPlot
            onMouseLeave={() => {
              setLeyendaMes();
            }}
            height={300}
            width={300}
          >
            <VerticalGridLines />
            <HorizontalGridLines />
            <XAxis />
            <YAxis />
            <VerticalBarSeries
              onNearestX={(value, { index }) => {
                setLeyendaMes(chartMes[index]);
              }}
              data={chartMes}
            />
            {leyendaMes && (
              <Hint value={leyendaMes}>
                <div style={{ background: "grey" }}>
                  <p style={{ color: "white" }}>{"Mes" + leyendaMes.x}</p>
                  <p style={{ color: "white" }}>{leyendaMes.y}</p>
                </div>
              </Hint>
            )}
          </XYPlot>
        </Segment>
      )}
      {chartData && (
        <Segment>
          <Header>Observaciones del Mes por Día</Header>
          <XYPlot
            onMouseLeave={() => {
              setLeyenda();
            }}
            height={300}
            width={300}
          >
            <VerticalGridLines />
            <HorizontalGridLines />
            <XAxis />
            <YAxis />
            <VerticalBarSeries
              onNearestX={(value, { index }) => {
                setLeyenda(chartData[index]);
              }}
              data={chartData}
            />
            {leyenda && (
              <Hint value={leyenda}>
                <div style={{ background: "grey" }}>
                  <p style={{ color: "white" }}>{"Dia" + leyenda.x}</p>
                  <p style={{ color: "white" }}>{leyenda.y}</p>
                </div>
              </Hint>
            )}
          </XYPlot>
        </Segment>
      )}
      {chartRubros && (
        <Segment>
          <Header>Observaciones por Mes por Rubro</Header>
          <XYPlot
            stackBy="y"
            onMouseLeave={() => {
              setLeyendaMes();
            }}
            height={300}
            width={300}
          >
            <DiscreteColorLegend
              style={{ position: "absolute", right: "0px", top: "40px" }}
              items={lista}
            />
            <VerticalGridLines />
            <HorizontalGridLines />
            <XAxis />
            <YAxis />
            {chartRubros.map((item) => (
              <VerticalBarSeries
                onNearestX={(value, { index }) => {
                  setLeyendaMes(value);
                }}
                data={item}
              />
            ))}
            {leyendaMes && (
              <Hint value={leyendaMes}>
                <div style={{ background: "grey" }}>
                  <p style={{ color: "white" }}>{"Mes" + leyendaMes.x}</p>
                  <p style={{ color: "white" }}>{leyendaMes.y}</p>
                </div>
              </Hint>
            )}
          </XYPlot>
        </Segment>
      )}
    </Container>
  );
}

export default Home;
